@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;400&family=Permanent+Marker&display=swap');
body {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: #d2dbdd;
    font-family: "Josefin Sans", "Arial Narrow", Arial, sans-serif;
}

div {
    width: 100%;
    height: 100vh;
    background: #8e44ad;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1 {
    color: white;
    background: transparent;
    text-shadow: 0 2px 2px black;
}

input {
    width: 80%;
    padding: 10px 20px;
    border: none;
    outline: none;
    margin: 20px 0;
    text-align: center;
    font-family: "Josefin Sans", "Arial Narrow", Arial, sans-serif;
}

button {
    padding: 12px 20px;
    background: #9b59b6;
    color: white;
    border: 2px solid #ecf0f1;
    outline: none;
    border-radius: 5px;
    text-transform: 5px;
    cursor: pointer;
}